.parallax-effect {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 200px;
  height: 200px;
  color: white;
  border-radius: 20px;

  transform-style: preserve-3d;
}

.inner-element {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 35px;
  font-style: italic;
  color: white;
  transform: translateZ(60px);
}

img {
  height: 200px;
  width: 200px;
}

.typingeffect {
  font-size: 2rem;
  margin-bottom: 30px;
}

.social-icons {
  display: flex;
  margin-top: 50px;
}

.info a:hover {
  text-decoration: none;
}

@media only screen and (max-width: 600px) {
  .typingeffect {
    font-size: 1rem;
  }
  .social-icons {
    margin-bottom: 15px;
  }
}
